import CoinGecko from 'coingecko-api'
import React, {useState, useEffect} from 'react'
import Spinner from './Spinner'
import {Link } from "react-router-dom";
export default function Exchanges() {
    const CoinGeckoClient = new CoinGecko()
    const [exchangesList, setExchangesList] = useState([])
    const [isLoad, setIsLoad] = useState(false)
    useEffect(() => {
        let fetchDetails = async () => {
            setIsLoad(true)
            let {data} = await CoinGeckoClient.exchanges.all()
            let newData = data.map(exchange => ({
                id: exchange.id,
                name: exchange.name,
                img: exchange.image,
                country: exchange.country,
                vol24: exchange.trade_volume_24h_btc_normalized,
                score: exchange.trust_score_rank,
                url: exchange.url,
                year: exchange.year_established
            }))
            setExchangesList(newData)
            setIsLoad(false)
        }
        try {
            fetchDetails()
        } catch (error) {
            console.error(error)
        }               
    },[])
    const formated = x => new Intl.NumberFormat('en-US', { 
        style: 'currency', 
        currency: 'USD',
        minimumFractionDigits: 0,
        maximumFractionDigits: 2
      }).format(x)
    return (
        <div className="page">
            {
                isLoad ? <Spinner/> : 
            <table className="table">
                <thead>
                    <tr>
                        <th>score</th>
                        <th>exchange name</th>
                        <th>Volume(24h)</th>
                        <th>year established  </th>
                    </tr>
                </thead>
                <tbody>
            {exchangesList.map(exchange => {
                return <tr className='tr-wrap' key={exchange.id}>
                    <td ><p>{exchange.score || '-'}</p></td>
                    <td><h1><Link to={`/exchanges/${exchange.id}`}>{exchange.name || '-'}</Link></h1></td>
                    <td><p>{formated(exchange.vol24) || '-' }</p></td>
                    <td><p>{exchange.year || '-'}</p></td>
                </tr>
            })}
                </tbody>
            </table>
            }
        </div>
    )
}


